:root {
    --primary: #fff;
    --secondary: #1f351c;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--filled {
    background-color: var(--secondary);
    color: #fff;
    border: 1px solid var(--primary);
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 18px;
    border-radius: 6px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
    border-radius: 6px;
}

.btn--large:hover,
.btn--medium:hover {
    background: #fff;
    color: #1f351c;
    transition: all 0.2s ease-out;
    border: 2px solid var(--primary);
}

.btn--filled:hover {
    background: #fff;
    color: #1f351c;
    transition: all 0.2s ease-out;
    border: 2px solid var(--secondary);
}